@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.movies {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  padding-top: 70px;
  margin-top: 70px;
}

.movies .movie {
  background-color: black;
  margin-bottom: 70px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 600px;
  height: 500px;
  font-weight: 300;

  border-radius: 20px;
  position: relative;
  color: #b2b3bc;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  max-width: 400px;
  width: 100%;
  height: 100%;
  border-radius: 20px 0px 0px 20px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__data {
  padding: 10px;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0px;
  font-weight: 300;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
}

.movie .movie__title {
  color: white;
  font-weight: 600;
}
.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}
.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

.movie__subscribe {
  margin-top: 220px;
  border-radius: 20px;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 15px;
}

.movie__subscribe:hover {
  cursor: pointer;
  background-color: red;
  font-size: 18px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #13212a;
}

html,
body,
.root,
.container {
  height: 100%;
  display: flex;
}

.loader {
  color: white;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  margin-left: 750px;
  font-size: 60px;
}

.header {
  width: 100%;
  height: 70px;
  padding: 0px;
  margin: 0px;
  position: fixed;
  top: 0px;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #04070b;
  color: white;
  font-family: "Roboto", sans-serif;
}

.title__icon {
  margin-right: 20px;
  color: red;
  font-size: 40px;
}

.header__title {
  margin-left: 20px;
  font-size: 25px;
  font-weight: 600;
  display: flex;
  align-items: center;
  text-transform: uppercase;
}

.header__items {
  list-style: none;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.header__items li {
  margin-right: 30px;
  font-size: 18px;
  cursor: pointer;
}

.footer {
  position: fixed;
  bottom: 0;
  color: white;
  padding: 25px;
  margin-top: 20px;
  background-color: black;
  width: 100%;
  text-align: center;
}

